.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  padding: 20px;
  color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-header h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.App-header p {
  font-size: 1.2rem;
  max-width: 600px;
}

@media (max-width: 768px) {
  .App-header h1 {
    font-size: 2rem;
  }

  .App-header p {
    font-size: 1rem;
    padding: 0 20px;
  }
}